@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;700;900&display=swap);
body {
  /* background-color: #181c24; */
  /* background-color: black; #101010; */
  background: #070712;


  /* background: linear-gradient(180deg, #080c14 0%, #10141a 67%, #202020 100%); */
  margin: 0;
  /* font-size: 62.5%; */
  /* height: 100vh; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

canvas,
.canvas > div {
  z-index: 1;
}

.loading {
  padding: 10px;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}

/* table { max-width: 50vw; overflow: hidden } */

.App {
  color: #eaeaea;
  text-align: center;
  /* height: 100vh; */
}

.App ul {
  padding: 1px;
}

.App ul > li {
  list-style: none;
}

.App ul > li > span {
  margin-left: 12px;
}

.App button {
  padding: 14px;
  margin-right: 12px;
  border-width:1px;
  color:#fff;
  background-color: black;
  font-weight:bold;
  border-top-left-radius:5px;
  border-top-right-radius:5px;
  border-bottom-left-radius:5px;
  border-bottom-right-radius:5px;

}

.Dropdown-control { background: #303030 !important; color: #eaeaea !important }
.Dropdown-menu { background: #303030 !important; color: #eaeaea !important }
.Dropdown-option { background: #303030 !important; color: #eaeaea !important }

div[aria-label='View'] {
  display: flex;
  justify-content: center;
}

div[aria-label='Controls'] { padding: 12px; margin-top: auto }

.Model {
